import React, { useEffect, useState } from 'react';
import { Line } from 'react-chartjs-2';

import './Chart.css'

function Chart(props) {

    const [values, setValues] = useState([]);
    const [date, setDates] = useState([]);
    const [quantity, setQuantity] = useState([]);

    useEffect(() => {
        for (var i = 0; i < Object.keys(props.rawPrice.props).length; i++) {
            if (props.rawPrice.props[i] !== undefined) {
                if (parseInt(props.rawPrice.props[i].minBuyout) > 10000) {
                    values.push(props.rawPrice.props[i].minBuyout / 10000);
                }
                else {
                    values.push(props.rawPrice.props[i].minBuyout);
                }
            }
        }
        setValues([...values]);

        for (var j = 0; j < Object.keys(props.rawPrice.props).length; j++) {
            if (props.rawPrice.props[j] !== undefined) {
                quantity.push(props.rawPrice.props[j].quantity);
            }
        }
        setQuantity([...quantity]);

        for (var k = 0; k < Object.keys(props.rawPrice.props).length; k++) {
            if (props.rawPrice.props[k] !== undefined) {
                var dateTime = new Date(props.rawPrice.props[k].scannedAt);
                date.push(dateTime.getFullYear() + '-' + (dateTime.getMonth() + 1).toString().padStart(2, 0) + '-' + dateTime.getDate() + ' ' + dateTime.getHours().toString().padStart(2, 0) + ':' + dateTime.getMinutes().toString().padStart(2, 0));
            }
        }
        setDates([...date]);
        // eslint-disable-next-line
    }, []);

    const data = {
        labels: date,
        datasets: [
            {
                label: 'Price',
                data: values,
                fill: true,
                backgroundColor: 'rgba(255, 255, 255, 0.5)',
                borderColor: 'rgb(255, 255, 255)',
            },
            {
                label: 'Quantity',
                data: quantity,
                fill: false,
                backgroundColor: 'rgb(45, 181, 196)',
                borderColor: 'rgb(45, 181, 196)',
            },
        ],
    };

    const options = {
        scales: {
            yAxes: [
                {
                    ticks: {
                        beginAtZero: false,
                    },
                },
            ],
        },
    };

    return (
        <>
            <Line className="chart" data={data} options={options} />
        </>
    );
}

export default Chart;